<template>
    <section class="chart-container" style="text-align: center">
        <h3  style="margin-top: 50PX">What Are You Interests</h3>
        <h4 style="margin-top:30PX">Check All That Apply</h4>
         <div style="text-align: center">
            <el-checkbox v-model="checked3" label="1. Technology" border style="display: block;width: 200px;margin: 0 auto;margin-top: 10px;text-align: left;color: black"></el-checkbox>
            <el-checkbox v-model="checked4" label="2. Art" border style="display: block;width: 200px;margin: 0 auto;margin-top: 10px;text-align: left;color: black"></el-checkbox>
          </div>
        <el-button type="primary" style="font-size: 16px;position: absolute;  top: 150px; margin-left: 150px;"  @click="toCourses"> NEXT</el-button>

    </section>
</template>

<script>

    export default {
        data() {
            return {
                checked3: false,
                checked4: false,
                checked5: false,
                checked6: false,
                checkboxGroup5: [],
                checkboxGroup6: []
            }
        },

        methods: {
            toCourses(){
                console.log(11111)
                this.$router.push({path:'/courses'})
            }  
        },
    }
</script>

<style >
.el-checkbox__input  {
    margin-top: 4px;
    float: right !important;
}
    /*index*/
    .welinfo {
        height: 32px;
        padding-bottom: 8px;
    }

    .welinfo span {
        float: left;
    }

    .welinfo b {
        padding-left: 8px;
        line-height: 32px;
    }

    .welinfo a {
        padding-left: 15px;
        color: #3186c8;
    }

    .welinfo a:hover {
        color: #F60;
    }

    .welinfo i {
        font-style: normal;
        padding-left: 8px;
        line-height: 32px;
    }

    .xline{border-bottom:solid 1px #dfe9ee; height:5px;}

    .chart-container {
        width: 100%;
        float: left;
    }

    /*.chart div {
        height: 400px;
        float: left;
    }*/

    .el-col {
        padding: 30px 20px;
    }
</style>



