            <el-input type="text" placeholder="验证码,单击图片刷新" v-model="code" style="float: left;width:50%"></el-input>
            <div class="code" @click="refreshCode" style="float: right;margin-right: 10%;width:35%">
                <identify :identifyCode="identifyCode"></identify>
            </div>
        </el-form-item> -->
        <el-form-item prop="identify">
            <identify2 v-on:verification="verification"  :mouseMoveStata="identifyStatus"></identify2>
        </el-form-item>
        <el-checkbox v-model="checked" checked class="remember">Remember password</el-checkbox>
        <el-form-item style="width:100%;">
            <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">login</el-button>
            <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
        </el-form-item>
    </el-form>
</template>
<script>
import {
    requestLogin
} from '../api/api';
import identify2 from '../components/identify2';
import identify from '../components/identify';
//import NProgress from 'nprogress'
export default {
    data() {
            return {
                code: "",
                identifyCodes: "1234567890",
                identifyCode: "",
                logining: false,
                ruleForm2: {
                    account: 'admin',
                    checkPass: '123456'
                },
                rules2: {
                    account: [{
