                    ]
                },
                checked: true,
                identifyStatus:false
            };
        },
        components: {
            'identify': identify,
            "identify2": identify2
        },
        mounted() {
            this.identifyCode = "";
            this.makeCode(this.identifyCodes, 4);
        },
        methods: {
            verification: function(verification) {
                // childValue就是子组件传过来的值
                this.identifyStatus = verification;
                console.log(this.identifyStatus)
            },
            randomNum(min, max) {
                return Math.floor(Math.random() * (max - min) + min);
            },
            refreshCode() {
                this.identifyCode = "";
                this.makeCode(this.identifyCodes, 4);
            },
            makeCode(o, l) {
                for (let i = 0; i < l; i++) {
                    this.identifyCode += this.identifyCodes[
                        this.randomNum(0, this.identifyCodes.length)
                    ];
                }
                console.log(this.identifyCode);
            },

            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },
            handleSubmit2(ev) {
                var _this = this;
                // if (this.code != this.identifyCode) {
                //     this.$message({
                //         message: "登录失败，验证码错误",
                //         type: 'error'
                //     });
                //     this.code = ""
                //     this.refreshCode();
                //     return;
                // }
                if (!this.identifyStatus) {
                    this.$message({
                        message: "Login failed, please drag the slider to verify",
                        type: 'error'
                    });
                    return;
                }
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        //_this.$router.replace('/table');
                        this.logining = true;
                        //NProgress.start();
                        var loginParams = {
                            username: this.ruleForm2.account,
                            password: this.ruleForm2.checkPass,
                            status:"1"
                        };
                        if(this.ruleForm2.account !="admin"||this.ruleForm2.checkPass!=123456){
                            console.log(1111111)
                            this.$message({
                                    message: "Login failed, username or password is incorrect",
                                    type: 'error'
                                });
                        }else {
                            console.log(2222222)
                            this.$router.push({
                                    path: '/index'
                                });
                        }
                        // requestLogin(loginParams).then(data => {
                        //     console.log(data)
                        //     this.logining = false;
                        //     if (data.data =="") {
                        //         this.$message({
                        //             message: "登录失败，用户名或密码错误",
                        //             type: 'error'
                        //         });
                        //         // this.code = ""
                        //         // this.refreshCode();
                        //         this.identifyStatus = false;
                        //     } else {
                        //         sessionStorage.setItem('user', JSON.stringify(data.data));
                        //         this.$router.push({
                        //             path: '/Application'
                        //         });
                        //     }
                        // });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
}
</script>
<style lang="scss" scoped>
.code {
    width: 114px;
    height: 40px;
}

.login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
        margin: 0px auto 40px auto;
        text-align: center;
        color: #505458;
    }
    .remember {
        margin: 0px 0px 35px 0px;
    }
}
</style>



