import axios from 'axios';

// let base = 'http://2x3086554w.iask.in';
let base = 'http://localhost:3000';
let cloud = 'http://cat.lincec.com'


export const requestLogin = params => {
	return axios.post(base + "/user/findOne",params);
};
//user
export const getAllUser = params => {
	return axios.get(base + "/user/findAll", {
		params: params
	});
};
export const addUser = params => {
	return axios.post(base + "/user/insert", params);
};

export const editUser = params => {
	return axios.post(base + "/user/update", params);
};
export const removeUser = params => {
	return axios.post(base + "/user/delete", params);
};

export const batchremoveUser = params => {
	return axios.post(base + "/user/deleteMany", params);
};



